import React from 'react';
import { Layout, Divider,Spin,message} from "antd";
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import DropView from './DropView';
import HeadBarView from "@/components/HeadBar";
import './index.less';
import generateIteratively from '../../../network/generateIteratively'
import Fact from '@/model/fact'
import OperationType from '@/constant/OperationType'
import { genSubtitle, genFactSentence} from '../../../sentencer';
const { Header, Sider,Content,Footer} = Layout;

export default class EditView extends React.Component {
    constructor(props) {
        super(props);     
        this.state = {
            leftPaneWidth:'310px',
            rightPaneWidth:'21%',
            generateIterativeStart:false

        }
      }

      componentDidMount() {
          if(this.props.specIndex<1){
            this.generateIteratively()
        }
    }

    generateIteratively = async () => {
        this.props.generateStory([], [], '')
        //const { storyParameter, fileName, method } = this.props;
        const { fileName, algorithm,calSimilarityType} = this.props;
        let tempFacts = [];
        let relations=[];
        let tree = ""
        let startFact=""
        let endFact=""
        while(true) {
            this.setState({
                generateIterativeStart: true
            })
            const response = await generateIteratively(fileName, 6, '', startFact,endFact, tree, algorithm,"nofilter",calSimilarityType,1)
            
            if (response.data.fail) {
                message.info(response.data.fail)
                this.setState({
                    generateIterativeStart: false
                })
                return;
            }
            if (response.data.error) {
                message.info(response.data.error)
                this.setState({
                    generateIterativeStart: false
                })
                return;
            }
            let tempdata
            if(response.data.story) tempdata=response.data
            else tempdata=eval('('+response.data+')')
            if(tempdata.story.facts.facts==null){
                break;
            }
            const facts = tempdata.story.facts.facts;
            tree = tempdata.tree;
            startFact= tempdata.story.facts.facts[0]
            endFact= tempdata.story.facts.facts[tempdata.story.facts.facts.length-1]
            relations=[];
            tempFacts = [];
            for (let factDict of facts) {
                let fact = new Fact(
                    factDict['type'],
                    factDict['measure'],
                    factDict['subspace'],
                    factDict['groupby'],
                    factDict['focus'],
                    factDict['parameter'],
                    "", // chart
                    factDict['score'],
                    factDict['information'],
                    factDict['significance']
                )
                fact.generatedScript = genFactSentence(fact, this.props.intl.options.currentLocale)
                fact.generatedSubtitle =genSubtitle(fact,this.props.intl.options.currentLocale)
                tempFacts.push(fact);
                relations.push('none')


            }

                this.props.generateStory(tempFacts.slice(), relations, 1)
                if(tempdata.flag) break;
        }
        //生成故事结束
        this.setState({
            generateIterativeStart: false
        })

    }
    render() {
        const {initDone,intl, editstate,updateEdit,  } = this.props;
        let leftLayoutContent,leftLayoutHeader,leftLayoutFooter,rightLayout,rightLayout1
        // 记录当前时间并转成时间戳
        let now = new Date().getTime();
        // 从缓存中获取用户上次退出的时间戳
        let leaveTime = parseInt(localStorage.getItem('leaveTime'), 10);
        // 判断是否为刷新，两次间隔在5s内判定为刷新操作
        let refresh = (now - leaveTime) <= 3000;
        // 测试alert
        if(refresh){
            this.props.updateOperation(OperationType.BEFORE_UPLOAD)
            this.props.history.push("/")
        }
        window.onbeforeunload = function(e){
            if(e) e.returnValue=("重新加载此网站？系统可能不会保存你所做的更改");
            return "重新加载此网站？??系统可能不会保存你所做的更改"

        }
        window.onunload = function(){
            localStorage.setItem('leaveTime', new Date().getTime());
            };
        this.props.children.forEach(child => {
            if (child.props.layoutposition === 'right') {
                rightLayout = child;
            } else if (child.props.layoutposition === 'leftPannelContent') {
                leftLayoutContent = child;
            } else if (child.props.layoutposition === 'right1') {
                rightLayout1 = child;
            } else if (child.props.layoutposition === 'leftPannelHeader'){
                leftLayoutHeader=child;
            }else if (child.props.layoutposition === 'leftPannelFooter'){
                leftLayoutFooter=child;
            }
        });

        let tip = initDone && intl.get("Generating...");
        return <div className='EditPage'>
            <Header style={{ height: "50px" }}>
                <HeadBarView isLogIn={false} {...this.props} />
            </Header>
            <Spin tip={tip} spinning={updateEdit || this.state.generateIterativeStart} wrapperClassName="regenerateSpining">
            
            <Divider className="customDivider" />
            <DndProvider backend={HTML5Backend} options={{ enableMouseEvents: true }} className='splitPane-box'>
                <Layout style={{ height: "calc(100% - 50px)" }}>
                    <Layout className="leftPannel">
                        <Header className="leftPannelHeader" style={{height:this.props.isMobileEnd? "60px":"50px"}}>
                            {leftLayoutHeader}
                        </Header>
                        <Content style={{ borderRadius: "10px 10px 0px 0px",backgroundColor: "white",boxShadow:"0px 3px 10px rgba(201, 201, 201, 0.5)", height:"calc(100% - 265px)",overflow:'hidden'}}>{leftLayoutContent}</Content>
                        {/* <Content style={{ borderRadius: "10px 10px 0px 0px",backgroundColor: "white",boxShadow:"0px 0px 2px 1px #e6e5e59b", height:"calc(100% - 265px)",overflow:'hidden'}}>{leftLayoutContent} </Content> */}
                        <Footer className="leftPannelFooter" style={{ borderRadius: "0px 0px 10px 10px",backgroundColor: "white",display: this.props.isHiddenFactView ? 'none' : 'block' , marginBottom:!this.props.isMobileEnd&&this.props.isHiddenFactView? "55px":"10px"}}>     
                            <DropView {...this.props}>{leftLayoutFooter}</DropView>           
                        </Footer> 
                    </Layout>
                    <Sider  width='308' style={{ display: this.props.isHiddenFactView ? 'none' : 'block' }}>
                        {
                            editstate ? (<div className='rightPannel1'>{rightLayout1}</div>) : (<div className="rightPannel">{rightLayout}</div>)
                        }
                    </Sider>
                    {/* <div style={{ display: this.props.isHiddenFactView ? "flex" : "none" }} className='mobileTabView'>
                        <MobileTab {...this.props} />
                    </div> */}
                </Layout>
            </DndProvider>
                 </Spin>
        </div >
    }
}